import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { login } from "../../redux/auth-reducer";
import { InputArea } from "../common/FormsControl/TextArea";
import { requiredCreater } from "../utils/validator/validators";
import { Navigate } from "react-router-dom";
import style from "./login.module.css";
let requiredField = requiredCreater();
const Login = (props) => {
  const { handleSubmit } = props;
  return (
    <form onSubmit={handleSubmit}>
      <div>
        <Field
          placeholder={"email"}
          name={"email"}
          component={InputArea}
          validate={requiredField}
        />
      </div>
      <div>
        <Field
          placeholder={"Password"}
          name={"password"}
          type="password"
          component={InputArea}
          required
        />
      </div>
      <div className={style.checkarea}>
        <Field component={InputArea} type={"checkbox"} name={"rememberMe"} />
        Please check if you want to continue
      </div>
      {props.error && (
        <div className={style.formsummaryerror}>{props.error}</div>
      )}
      <div>
        <button>Login</button>
      </div>
    </form>
  );
};
const LoginReduxForm = reduxForm({
  form: "login",
})(Login);
const LoginForm = (props) => {
  const onSubmit = (formData) => {
    props.login(formData.email, formData.password, formData.remeberMe);
  };
  if (props.isAuth) {
    return <Navigate to={"/profile"} />;
  }
  return (
    <div>
      <h1>Login</h1>
      <LoginReduxForm onSubmit={onSubmit} />
    </div>
  );
};
const mapStateToProps = (state) => {
  return {
    isAuth: state.auth.isAuth,
  };
};
export default connect(mapStateToProps, { login })(LoginForm);
